/* Installation.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.installation {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 20px;
  color: #004080;
}

.installation-content {
  display: flex;
  max-width: 1000px;
  width: 100%;
}

.installation-image img {
  width: 100%;
  max-width: 300px;
  border-radius: 10px;
}

.installation-text {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.installation-title {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 20px;
}

.installation-description {
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  margin-bottom: 20px;
  padding-left: 20px;
}

.installation-button {
  background-color: #ffcc00;
  color: #004080;
  border: none;
  padding: 10px 20px;
  border-radius: 50px; /* Oval shape */
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  cursor: pointer;
  width: fit-content;
}

.installation-button:hover {
  background-color: #e6b800;
}
