/* Header.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.header {
  display: flex;
  justify-content: space-between;
  padding: 120px;
  background: linear-gradient(90deg, #001f3f, #004080);
  color: white;
}

.header-content {
  max-width: 50%;
}

.header-content h1 {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-weight: 800;
  margin-bottom: 24px;
  text-align: left;
}

.header-description {
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  margin-bottom: 24px;
  text-align: left;
}

.header-buttons {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.header-buttons button {
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 800;
  cursor: pointer;
}

.header-buttons .try-button {
  background-color: #ffcc00;
  color: #004080;
  border: none;
  margin-right: 10px;
}

.header-buttons .try-button:hover {
  background-color: #e6b800;
}

.header-buttons .demo-button {
  background-color: white;
  color: #004080;
  border: none;
}

.header-buttons .demo-button:hover {
  background-color: #f0f0f0;
}

.trial-info {
  font-size: 14px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  color: rgb(212, 207, 207);
  margin-top: 10px;
  text-align: left;
  font-style: italic;
}

.header-image img {
  max-width: 100%;
}

.highlight {
  font-weight: bold;
  color: #ffcc00;
}
