/* FAQ.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.faq-section-long {
  padding: 0px 150px;
  text-align: center;
  background: white;
}

.faq-container-long {
  padding-bottom: 100px;
  margin-bottom: 0px;
}

.faq-item-long {
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
  cursor: pointer;
}

.faq-question-long {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  color: #004080;
}

.faq-answer-long {
  margin-top: 10px;
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  color: #004080;
  text-align: left;
}
