/* Features.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.features {
  padding: 50px 20px;
  text-align: center;
}

.advantages-title {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0px;
}

.features-title {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  margin-bottom: 40px;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: 0px;
}

.advantages-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.advantage-card {
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  margin: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Slight shadow */
  width: 20%; /* Adjust width as necessary for your layout */
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the left */
}

.advantage-icon {
  width: 40px; /* Adjust size as needed */
  height: 40px; /* Adjust size as needed */
  margin-bottom: 10px;
}

.advantage-title {
  font-size: 24px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  color: #004080;
  margin-bottom: 0px;
  text-align: left;
}

.advantage-description {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  color: #004080;
  text-align: left;
}
