/* NavBar.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000&display=swap");

.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 25px 0px;
  background: transparent;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border-radius: 50px;
  padding: 10px 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 90%;
}

.nav-left {
  display: flex;
  align-items: center;
}

.nav-links {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-left: 20px; /* Espace entre le logo et les liens */
}

.nav-links li {
  margin: 0 10px;
}

.nav-links a {
  color: #004080;
  text-decoration: none;
  font-size: 20px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
}

.nav-logo {
  width: 200px;
  height: auto;
}

.nav-buttons {
  display: flex;
  align-items: center;
}

.try-button,
.demo-button {
  border: none;
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  cursor: pointer;
  margin-left: 10px;
}

.try-button {
  background-color: #ffcc00;
  color: #004080;
}

.try-button:hover {
  background-color: #e6b800;
}

.demo-button {
  background-color: white;
  color: #004080;
  border: 2px solid #004080;
}

.demo-button:hover {
  background-color: #f0f0f0;
}
