/* Contact.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.contact-section {
  padding: 50px 20px;
  color: white;
}

.contact-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  background: linear-gradient(90deg, #001f3f, #004080);
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Slight shadow */
  padding: 40px;
}

.contact-info {
  flex: 1;
  padding-right: 20px;
}

.contact-title {
  font-size: 24px;
  margin-bottom: 20px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
}

.contact-description {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  margin-bottom: 20px;
}

.contact-buttons {
  display: flex;
  gap: 10px;
}

.contact-button {
  background-color: #ffcc00;
  color: #004080;
  border: none;
  padding: 10px 20px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
}

.contact-button.secondary {
  background-color: white;
  color: #004080;
  border: 2px solid #004080;
}

.contact-button:hover {
  opacity: 0.9;
}

.contact-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-image img {
  width: 200px; /* Adjust size as needed */
  height: 200px; /* Adjust size as needed */
  border-radius: 15px;
}
