@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.cards {
  padding: 50px 20px;
  text-align: center;
}

.section-title {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0px;
}

.section-subtitle {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-weight: 800;
  margin-top: 0px;
  margin-bottom: 40px;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cards-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.large-card {
  display: flex;
  align-items: center;
  width: 90%;
  background: linear-gradient(90deg, #001f3f, #004080);
  border-radius: 10px;
  padding: 20px;
  margin: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
  text-align: left;
}

.large-card .card-image {
  width: 50%; /* Agrandir l'image */
  border-radius: 10px;
  margin-right: 20px; /* Espace entre l'image et le texte */
}

.large-card .card-content {
  width: 60%; /* Ajustez ce pourcentage selon vos besoins */
}

.small-cards-container {
  display: flex;
  justify-content: space-between;
  width: 93%;
  margin-top: 20px;
}

.small-card {
  width: 45%;
  background: linear-gradient(90deg, #001f3f, #004080);
  border-radius: 10px;
  padding: 20px;
  margin: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
  text-align: left;
}

.small-card .card-image {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 10px;
}

.card-title {
  font-size: 24px;
  font-family: "Dosis", sans-serif;
  font-weight: 800;
  margin-bottom: 10px;
}

.card-description {
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  margin-bottom: 20px;
}
