/* Numbers.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.metrics-container {
  display: flex;
  justify-content: center;
  text-align: center;
  padding: 50px 20px;
}

.metric-card {
  margin: 0 20px;
}

.metric-title {
  font-size: 48px;
  font-family: "Dosis", sans-serif;
  font-weight: 800;
  color: #004080;
  margin-bottom: 10px;
}

.metric-description {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  color: #004080;
  margin-top: 0;
}
