/* Footer.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.footer {
  background: linear-gradient(90deg, #001f3f, #004080);
  padding: 20px 0;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
}

.social-icons {
  display: flex;
}

.social-icons a {
  color: white;
  margin-right: 15px;
  font-size: 24px;
  text-decoration: none;
}

.social-icons a:hover {
  color: #ffcc00; /* Optional: add a hover color */
}

.footer-columns {
  display: flex;
}

.footer-column {
  margin-left: 40px;
}

.footer-title {
  font-size: 18px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  margin-bottom: 10px;
}

.footer-list {
  list-style: none;
  padding: 0;
}

.footer-list li {
  margin-bottom: 5px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
}

.footer-list a {
  color: white;
  text-decoration: none;
}

.footer-list a:hover {
  text-decoration: underline;
}

/* Import Font Awesome for icons */
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css");
